.content{
  width: 85.6%;
  margin: 0 auto;
}
@media (max-width:1200px){
  .content{
    width: calc(100% - 64px);
  }
}
@media (max-width:810px){
  .content{
    width: calc(100% - 40px);
  }
}
.linkBlock{
  width: 64px;
  margin-bottom: 48px;
}
@media (max-width:810px){
  .linkBlock{
    margin-bottom: 32px;
  }
}
.link{
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #909090;
text-decoration: none;
font-size: 16px;
font-weight: 400;
line-height: 20px; /* 125% */
transition: all ease .3s;
}
@media(hover: hover){
.link:hover .svg path{
  fill: #fff
}
}
@media(hover: hover){
.link:hover span{
  color: #fff;
}
}

.svg{
  display: flex;
  align-items: center;
  justify-content: center;
}
.svg svg path{
  fill: #909090;

  transition: all ease .3s;
}
.link span{
  transition: all ease .3s;
}
.header{
  display: flex;
  align-items: end;
  justify-content: space-between;
  margin-bottom: 48px;
  gap: 120px;
}
@media (max-width:810px){
  .header{
    flex-direction: column;
    gap: 32px;
    align-items: start;
    margin-bottom: 32px;
  }
}
.titleBlock{
  max-width: 812px;
}
.title{
font-size: 40px;
font-weight: 400;
line-height: 44px; /* 110% */
margin-bottom: 20px;

}
@media (max-width:1200px){
  .title{
    font-size: 32px;
    line-height: 36px;
  }
}
.subtitle{
  color: #909090;
font-size: 16px;
font-weight: 300;
line-height: 24px; /* 150% */
}
.list{
  flex: 0 0 auto;
  min-width: 30%;
}
.list li{
  list-style: none;
}
.list li:last-child{
  margin-bottom: 0;
}
.listItem{
  display: flex;
  align-items: center;
  color: #909090;
  font-size: 16px;
  font-weight: 300;
  line-height: 20px; /* 125% */
  margin-bottom: 16px;
}
.listItem::before{
  margin-right: 12px;
  content: '';
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #fff;
}
.listItem span{
  color: #FFF;
  margin-left: 8px;
}
.homeImg img{
width: 100%;
object-fit: cover;
}
.homeImg{
  margin-bottom: 64px;
  overflow: hidden;
  border-radius: 12px;
}
@media (max-width:810px){
  .homeImg{
    margin-bottom: 36px;
    border-radius: 8px;
  }
}

.comingSoon{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  max-width: 812px;
  margin: 0 auto;
  gap: 20px;
  margin-bottom: 80px;
}
@media (max-width:1200px){
  .comingSoon{
    margin-bottom: 64px;
  }
}
@media (max-width:810px){
  .comingSoon{
    margin-bottom: 44px;
  }
}
.comingSoonTitle{
font-size: 40px;
font-weight: 400;
line-height: 44px; /* 110% */
}
@media (max-width:1200px){
  .comingSoonTitle{
    font-size: 32px;
    line-height: 36px;
  }
}
.comingSoonText{
  color: #909090;
font-size: 16px;
font-weight: 300;
line-height: 24px; /* 150% */
text-align: center;
}

.finalyBlock{
  padding-top: 48px;
  margin-bottom: 48px;
}
.resultBlock{
  align-items: start;
  display: flex;
  justify-content: center;
  gap: 48px;
  margin-bottom: 64px;
}
@media (max-width:810px){
  .resultBlock{
    flex-direction: column;
    gap: 32px;
    margin-bottom: 36px;
  }
}
.resultTilte{
  width: 50%;
font-size: 40px;
font-weight: 400;
line-height: 44px; /* 110% */
}
@media (max-width:1200px){
  .resultTilte{
    font-size: 32px;
  }
}
.resultList{
  width: 50%;
  font-size: 20px;
font-weight: 400;
line-height: 24px; /* 120% */
}
@media (max-width:810px){
  .resultList{
    width: 100%;
  }
}
.resultList li{
  margin-bottom: 12px;
  list-style: none;
  display: flex;
  align-items: start;
  
}
.resultList li::before{
  content: '';
  display: block;
  margin-top: 10px;
  min-width: 4px;
  min-height: 4px;
  border-radius: 50%;
  background: #fff;
  margin-right: 12px;
}
.resultList li:last-child{
  margin-bottom: 0px;
}
.finalyBlock{}
@media (max-width:810px){
  .finalyBlock{
    padding-top: 32px;
    margin-bottom: 32px;
  }
}

.title{

}
@media (max-width:1200px){
  .title{
    font-size: 32px;
    line-height: 36px;
  }
}
@media (max-width:810px){
  .title{
    margin-bottom: 20px;
  }
}
.works{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
  margin-bottom: 48px;
  padding-top: 28px;
  }
  @media (max-width:1200px){
    .works{
      padding-top: 12px;
    }
  }
  @media (max-width:810px){
    .works{
      flex-direction: column;
      gap: 32px;
      margin-bottom: 32px;
    }
  }
  .NavLink{
    width: 50%;
    text-decoration: none;
    color: #fff;
  }
  @media (max-width:810px){
    .NavLink{
      width: 100%;
    }
  }
  .item{
    
    transition: all ease .3s;
    overflow: hidden;
    cursor: pointer;
  }
  @media(hover: hover){
  .item:hover .img{
  transform: scale(1.07);
  }
  }


  .imgBlock{
    border-radius: 8px;
    width: 100%;
    overflow: hidden;
    margin-bottom: 24px;
  
  }
  .img{
    width: 100%;
    
    image-rendering: -moz-crisp-edges; /* Firefox */
  -ms-interpolation-mode: nearest-neighbor;  /* IE */
  image-rendering: crisp-edges; /* Стандартное свойство */
    transition: all ease .5s;
  }
  .img img{
    width: 100%;
    object-fit: cover;
  }
  
  .info{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    margin-bottom: 8px;
  
  }
  .icon{
    margin-right: 8px;
  }
  .place{
    color: #909090;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px; /* 133.333% */
  letter-spacing: 0.8px;
  text-transform: uppercase;
  }
  .date{
    color: #909090;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 133.333% */
  letter-spacing: 0.8px;
  text-transform: uppercase;
  flex-grow: 3;
  text-align: end
  }
  .subtitleWork{
  font-size: 20px;
  font-weight: 400;
  line-height: 24px; 
  }